.theme-changer {
    margin-right: 25px;
}

.theme-changer input[type="checkbox"] {
	visibility: hidden;
	&:checked + label {
		transform: rotate(360deg);
		background-color: #000;
		&:before {
			transform: translateX(30px);
			background-color: #FFF;
		}
	}
}

.theme-changer  label {
	display: flex;
	width: 60px;
	height: 30px;
	border: 2px solid;
	border-radius: 99em;
	position: relative;
	transition: transform .75s ease-in-out;
	transform-origin: 50% 50%;
	cursor: pointer;
	
	&:before {
		transition: transform .75s ease;
		transition-delay: .5s;
		content: "";
		display: block;
		position: absolute;
		width: 18px;
		height: 18px;
		background-color: #000;
		border-radius: 50%;
		top: 4px;
		left: 4px;
	}
}